import { Component } from '@angular/core';
import { NavController, NavParams, ActionSheetController, Platform, ToastController, LoadingController } from 'ionic-angular';
import { DashboardPage } from '../dashboard/dashboard';
import { AuditionProvider } from '../../providers/audition/audition';
import { GlobalVariablesProvider } from '../../providers/global-variables/global-variables';
import { FileChooser } from '@ionic-native/file-chooser';
import { IOSFilePicker } from '@ionic-native/file-picker';
import { File } from '@ionic-native/file';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';

/**
 * Generated class for the UploadeventPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-uploadevent',
    templateUrl: 'uploadevent.html',
})
export class UploadeventPage {
    public min = new Date().toJSON().split('T')[0];;
    imageFile: String = "assets/imgs/newalert.jpg";
    public submitAttempt: boolean = false;
    public event =
    {
        auditionName: "",
        auditionDate: "",
        auditionUrl: "",
        userId: null,
        auditionDescription: "",
        auditionImage: ""
    };
    public auditionForm: FormGroup;
    public auditionImage: any;
    public userId: any;
    public base64Image = "data:image/jpeg;base64,";

    constructor(public navCtrl: NavController, public navParams: NavParams, public globalVariables: GlobalVariablesProvider, public toast: ToastController, public auditionProvider: AuditionProvider, public loadingCtrl: LoadingController, public fileChooser: FileChooser, public platform: Platform, public iosFilePicker: IOSFilePicker, public camera: Camera, public actionSheetCtrl: ActionSheetController, public formBuilder: FormBuilder) {
        this.userId = this.globalVariables.getUserId();
        this.auditionForm = formBuilder.group({
            auditionDescription: [''],
            auditionDate: ['', Validators.compose([Validators.required])],
            auditionName: ['', Validators.compose([Validators.required])],
            auditionUrl: [''],
        });
    }
    ionViewDidLoad() {
        console.log('ionViewDidLoad UploadeventPage');
    }

    openeditprofile() {
        let actionSheet = this.actionSheetCtrl.create({

            buttons: [
                {
                    text: 'Take Photo',
                    role: 'destructive',
                    icon: !this.platform.is('ios') ? 'ios-camera-outline' : null,
                    handler: () => {
                        this.captureImage(false);
                    }
                },
                {
                    text: 'Choose Photo From Gallery',
                    icon: !this.platform.is('ios') ? 'ios-images-outline' : null,
                    handler: () => {
                        this.captureImage(true);
                    }
                }, {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {
                        console.log('Cancel clicked');
                    }
                }
            ]
        });
        actionSheet.present();
    }

    async captureImage(useAlbum: boolean) {
        const options: CameraOptions = {
            quality: 100,
            destinationType: this.camera.DestinationType.DATA_URL,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            ...useAlbum ? { sourceType: this.camera.PictureSourceType.SAVEDPHOTOALBUM } : {}
        }

        this.camera.getPicture(options).then((imageData) => {
            this.base64Image = 'data:image/jpeg;base64,' + imageData;
        }, (err) => {
            this.showError(err);
        });

    }

    showError(str) {
        let toast = this.toast.create({
            message: str,
            duration: 10000,
            position: 'bottom'
        });
        toast.present();
    }

    uploadPoster() {
        this.submitAttempt = true;
        if(this.auditionForm.valid){

       
        var loader = this.loadingCtrl.create({
            content: "Please wait..."
        });

       loader.present();
        this.auditionProvider.uploadAuditionImage(this.base64Image).subscribe((response: any) => {
            this.event.auditionImage = response;
            this.event.userId = this.userId;
            this.event.auditionDate = this.event.auditionDate +' '+ new Date().getHours() +':'+ Number(new Date().getMinutes() + 1) + ':' + new Date().getSeconds();
           this.auditionProvider.uploadEvent(this.event).subscribe((response: any) => {
                loader.dismiss();
                if (response.result == true) {
                    this.showError("Your event has been shared successful.");
                    this.navCtrl.setRoot(DashboardPage);
                }
                else {
                    this.showError("We are unable to upload your event at the moment please try again later.");
                }
            });
        })
        }
    }
}



